import React from 'react';

type DividerProps = {
  width?: string;
  marginTop?: string;
  marginBottom?: string;
  backgroundColor?: string;
};

/**
 *    
 * @param width:  
 * @param marginTop:   
 * @param marginBottom:   
 * @param backgroundColor:  
 * @returns 
 */
const Divider = ({
  width = '100%',
  marginTop = '0px',
  marginBottom = '0px',
  backgroundColor = '#eaeaea',
}: DividerProps) => {
  return (
    <div
      style={{
        width: width,
        height: '1px',
        backgroundColor: backgroundColor,
        marginTop,
        marginBottom,
      }}
    ></div>
  );
};

export default Divider;
